var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('v-container',[_c('h1',[_vm._v("绯红女巫")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('div',{staticStyle:{"display":"flex","flex-direction":"row"}},[_c('div',{staticStyle:{"display":"flex","flex-direction":"column"}},[_c('blockquote',{staticClass:"blockquote"},[_vm._v("        Scarlet Witch是Marvel Comics出版的美国漫画书中出现的虚构超级英雄。该角色由作家斯坦李和艺术家杰克柯比创作，首次出现在漫画书银色时代的X战警＃4（1964年3月）。她首先被描绘成一个超级恶棍，以及她的双胞胎兄弟Quicksilver作为Mutants兄弟会的创始成员。在大多数描绘中，她被描绘成一个突变体，一个具有超人能力的人类虚构亚种的成员并且，对于角色的大部分历史，被认为是突变体Magneto的女儿。猩红女巫拥有以未指明的方式改变现实的能力，是一位强大的女巫。Scarlet Witch后来被描述为复仇者联盟超级英雄团队的常规成员。她也成为超级英雄和队友Vision的妻子，她有两个儿子，托马斯和威廉。")]),_c('blockquote',{staticClass:"blockquote"},[_vm._v("        角色的宇宙背景故事和亲子关系不止一次被改变。作为黄金时代超级英雄Whizzer的孩子在20世纪70年代被描绘，20世纪80年代的一个侦察者发现Scarlet Witch和Quicksilver是supervillain Magneto的未知后代。出生于Magneto在Transia的疏远妻子，Scarlet Witch和她的兄弟留在他们的养父罗马尼父母的照料下，她被养成了Wanda Maximoff（后来被称为Wanda Frank）而Whizzer被认为是她的父亲）。在2010年的另一个侦察中，据透露，Quicksilver和她不是突变体，而是被高级进化论者绑架并用作遗传实验的对象，然后被误导相信Magneto是他们的父亲。")])]),_c('v-img',{staticClass:"stage-img",attrs:{"width":"195","height":"260","src":""}})],1),_c('br'),_c('h2',[_vm._v("创作背景")]),_c('hr',{staticClass:"mt-3 v-divider theme--light"}),_c('br'),_c('div',{staticStyle:{"display":"flex","flex-direction":"row"}},[_c('div',{staticStyle:{"display":"flex","flex-direction":"column"}},[_c('blockquote',{staticClass:"blockquote",staticStyle:{"margin-top":"-20px"}},[_vm._v("        猩红色巫婆首次亮相，连同她的哥哥，水银，作为一部分邪恶的突变体兄弟在X战警＃4（1964年3月）。他们被描述为不情愿的恶棍，对马格纳托的意识形态不感兴趣。猩红女巫被描绘成平静和顺从，就像当时大多数女性漫画人物一样。她的服装主要由带泳衣的泳衣，歌剧手套，短靴，覆盖她身体的紧身衣，超级英雄斗篷和简单的衣服组成，所有这些都是用红色调着色。所有那些早期的X战警角色都是由Stan Lee和Jack Kirby创造的。")]),_c('blockquote',{staticClass:"blockquote",staticStyle:{"margin-top":"-20px"}},[_vm._v("        李还编写了复仇者漫画书，由社论中最杰出的英雄组成。除了美国队长之外，他最终将所有这些都移除了，并用其他漫画中的恶棍取代了他们：来自X战警的血色女巫和Quicksilver，以及来自钢铁侠的故事中的 Hawkeye 故事。")])]),_c('v-card',{staticClass:"card-img"},[_c('v-img',{attrs:{"width":"180"